import { CodeSnippet } from '../../../components/CodeSnippet/CodeSnippet.js';
import { Card, Typography } from 'antd';
import type { FC } from 'react';

const { Title, Text, Paragraph } = Typography;

type InstructionProps = {
  application: string;
  deploymentKey: string;
  deploymentName: string;
};

const Instruction: FC<InstructionProps> = ({
  application,
  deploymentKey,
  deploymentName,
}) => {
  return (
    <Card>
      <Title level={4} style={{ marginTop: 0 }}>
        Make your first release
      </Title>
      <Text>
        Before making a release, you need to configure your application and CLI.
        Read more about it in the instructions.
      </Text>
      <Title level={5} style={{ marginTop: 16 }}>
        Setup deployment key for iOS
      </Title>
      <Paragraph>
        Add the <Text code>CodePushDeploymentKey</Text>to the file{' '}
        <Text code>ios/[ProjectName]/Info.plist</Text>
      </Paragraph>
      <CodeSnippet
        showLineNumbers
        language="xml"
        code={`<key>CodePushDeploymentKey</key>\n<string>${deploymentKey}</string>`}
      />
      <Title level={5} style={{ marginTop: 16 }}>
        Setup deployment key for Android
      </Title>
      <Paragraph>
        Add the <Text code>CodePushDeploymentKey</Text> to the file{' '}
        <Text code>android/app/src/main/res/values/strings.xml</Text>
      </Paragraph>
      <CodeSnippet
        showLineNumbers
        language="xml"
        code={`<resources>
  <string moduleConfig="true" name="CodePushDeploymentKey">${deploymentKey}</string>
</resources>`}
      />
      <Title level={5} style={{ marginTop: 16 }}>
        Push release
      </Title>
      <Paragraph>
        Run platform specific command from root of your application
      </Paragraph>
      <Text>For iOS:</Text>
      <CodeSnippet
        language="bash"
        code={`code-push release-react ${application} ios -d ${deploymentName}`}
      />
      <br />
      <Text>For Android:</Text>
      <CodeSnippet
        language="bash"
        code={`code-push release-react ${application} android -d ${deploymentName}`}
      />
    </Card>
  );
};

export default Instruction;
